.button {
  border: none;
  margin: 0;
  padding: 0;
  font-family: $ff-roboto;
  cursor: pointer;

  @include hover {
    opacity: 0.6;
  }

  &--promo {
    color: $color-alice-blue;
    background-color: $color-deep-sky-blue;
    font-family: $ff-roboto;
    text-transform: uppercase;
    border-radius: 3px;
    display: block;
    text-align: center;
  }

  &__text {
    font-family: $ff-roboto;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
  }

  @media (min-width: $desktop-width-min) {
    &--popup {
      background-color: rgba(0, 125, 215, 0);
      color: $color-alice-blue;
      border: 1px solid $color-alice-blue;
      border-radius: 3px;
      width: 160px;
      min-height: 40px;
      font-size: 15px;
      font-weight: 700;
      text-transform: uppercase;
      margin-right: 62px;
      margin-top: 35px;

      @include focus {
        background-color: $color-deep-sky-blue;
        outline: none;
      }
    }

    &--promo {
      @include focus {
        background-color: $color-alice-blue;
        outline: none;
        color: $color-black-pearl;
      }
    }

    &--about-company {
      @include focus {
        background-color: $color-deep-sky-blue;
        color: $color-alice-blue;
        outline: none;
      }
    }
  }

  @media (min-width: $tablet-width-min) {
    &--promo {
      width: 410px;
      min-height: 55px;
      font-size: 18px;
      padding-top: 15px;
    }

    &__text-tablet {
      display: block;
    }

    &__text-mobile {
      display: none;
    }
  }

  @media (min-width: $mobile-width-min) {
    &--promo {
      width: 100%;
      min-height: 40px;
      font-weight: 700;
      font-size: 14px;
      padding-top: 9px;
      letter-spacing: 1px;
    }

    &__text-tablet {
      display: none;
    }

    &__text-mobile {
      display: block;
    }
  }

  @media (min-width: $mobile-width-min) and (max-width: $mobile-width-max) {
    &--minus-icon {
      &::before {
        content: "";
        position: absolute;
        display: block;
        width: 15px;
        height: 15px;
        right: 0;
        background-image: url("/img/svg/minus-icon.png");
      }
    }

    &--plus-icon {
      &::before {
        content: "";
        position: absolute;
        display: block;
        width: 15px;
        height: 15px;
        right: 0;
        background-image: url("/img/svg/plus-icon.png");
      }
    }
  }
}
